import Link from 'next/link';
import Image from 'next/image';

const categories = [
  {
    id: 'equipment',
    name: 'Equipment',
    description: 'Professional-grade gym equipment for your home or commercial gym.',
    image: '/images/category-equipment.jpg',
    itemCount: 50,
  },
  {
    id: 'accessories',
    name: 'Accessories',
    description: 'Essential accessories to enhance your workout experience.',
    image: '/images/category-accessories.jpg',
    itemCount: 30,
  },
  {
    id: 'supplements',
    name: 'Supplements',
    description: 'Premium supplements for optimal performance and recovery.',
    image: '/images/category-supplements.jpg',
    itemCount: 25,
  },
  {
    id: 'clothing',
    name: 'Clothing',
    description: 'High-performance workout apparel for maximum comfort.',
    image: '/images/category-clothing.jpg',
    itemCount: 40,
  },
];

export default function CategoriesPage() {
  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-teal-800 mb-4">Shop by Category</h1>
        <p className="text-lg text-teal-600 max-w-2xl mx-auto">
          Explore our wide range of fitness products across different categories to find exactly what you need for your fitness journey.
        </p>
      </div>

      {/* Categories Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {categories.map((category) => (
          <Link
            key={category.id}
            href={`/categories/${category.id}`}
            className="group"
          >
            <div className="relative overflow-hidden rounded-lg shadow-lg hover:shadow-xl transition-shadow duration-300 border border-teal-100">
              <div className="relative h-64">
                <Image
                  src={category.image}
                  alt={category.name}
                  fill
                  className="object-cover group-hover:scale-105 transition-transform duration-300"
                />
                <div className="absolute inset-0 bg-gradient-to-t from-teal-900/80 to-transparent" />
              </div>
              <div className="absolute bottom-0 left-0 right-0 p-6 text-white">
                <div className="flex justify-between items-end">
                  <div>
                    <h3 className="text-2xl font-bold mb-2 text-orange-400">{category.name}</h3>
                    <p className="text-teal-100 mb-2">{category.description}</p>
                    <span className="text-sm font-medium text-orange-400">
                      {category.itemCount} Products
                    </span>
                  </div>
                  <svg
                    className="w-6 h-6 text-orange-400 transform group-hover:translate-x-2 transition-transform duration-300"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M9 5l7 7-7 7"
                    />
                  </svg>
                </div>
              </div>
            </div>
          </Link>
        ))}
      </div>

      {/* Featured Section */}
      <div className="mt-16">
        <h2 className="text-2xl font-bold text-teal-800 mb-8">Featured Collections</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {/* New Arrivals */}
          <div className="bg-teal-50 rounded-lg p-6 border border-teal-100">
            <h3 className="text-lg font-semibold text-teal-800 mb-2">New Arrivals</h3>
            <p className="text-teal-600 mb-4">Check out our latest products across all categories.</p>
            <Link
              href="/products?sort=newest"
              className="text-orange-500 font-medium hover:text-orange-600 flex items-center"
            >
              Browse New Arrivals
              <svg
                className="w-4 h-4 ml-2"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M9 5l7 7-7 7"
                />
              </svg>
            </Link>
          </div>

          {/* Best Sellers */}
          <div className="bg-teal-50 rounded-lg p-6 border border-teal-100">
            <h3 className="text-lg font-semibold text-teal-800 mb-2">Best Sellers</h3>
            <p className="text-teal-600 mb-4">Discover our most popular products loved by customers.</p>
            <Link
              href="/products?sort=popular"
              className="text-orange-500 font-medium hover:text-orange-600 flex items-center"
            >
              View Best Sellers
              <svg
                className="w-4 h-4 ml-2"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M9 5l7 7-7 7"
                />
              </svg>
            </Link>
          </div>

          {/* Special Offers */}
          <div className="bg-teal-50 rounded-lg p-6 border border-teal-100">
            <h3 className="text-lg font-semibold text-teal-800 mb-2">Special Offers</h3>
            <p className="text-teal-600 mb-4">Great deals and discounts on selected items.</p>
            <Link
              href="/products?sort=discount"
              className="text-orange-500 font-medium hover:text-orange-600 flex items-center"
            >
              Shop Deals
              <svg
                className="w-4 h-4 ml-2"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M9 5l7 7-7 7"
                />
              </svg>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
} 